html {
  color: #212121;
  background-color: #fff;
  font-family: verdana, arial, sans-serif;
  font-size: 22px;
  line-height: 1.3;
}

turbo-frame {
  display: block;
}

input, textarea, select, button {
  font-size: 100%;
}

a {
  color: #ec005a;
}

blockquote {
  border-left: 4px solid #ccc;
  margin-left: 10px;
  padding: 10px 20px;
}

.turbo-progress-bar {
  background-color: #ec005a;
}

.toaster {
  pointer-events: none;
  width: 450px;
  z-index: 2;
  position: fixed;
  bottom: 11px;
  left: 10px;
}

.toaster__toast {
  box-sizing: border-box;
  cursor: pointer;
  pointer-events: auto;
  -webkit-user-select: none;
  user-select: none;
  background-color: #fff;
  border: 1px solid #008ccb;
  border-left-width: 7px;
  border-radius: 0 2px 2px 0;
  margin-top: 10px;
  padding: 12px 20px;
  box-shadow: 2px 3px 5px #0000004d;
}

.toaster__toast.in {
  animation-name: toaster-toast-in;
  animation-duration: .3s;
  animation-timing-function: ease-out;
  animation-iteration-count: 1;
  animation-fill-mode: both;
}

.toaster__toast.out {
  animation-name: toaster-toast-out;
  animation-duration: .15s;
  animation-timing-function: ease-out;
  animation-iteration-count: 1;
  animation-fill-mode: both;
  overflow: hidden;
}

@keyframes toaster-toast-in {
  from {
    opacity: .5;
    transform: translateX(-100%);
  }

  to {
    opacity: 1;
    transform: translateX(0%);
  }
}

@keyframes toaster-toast-out {
  from {
    max-height: 50px;
    opacity: 1;
  }

  to {
    max-height: 0;
    opacity: 0;
    padding-top: 0;
    padding-bottom: 0;
  }
}

